<template>
  <div>
      <div class="gouwuche"  :class="{'bianhui' : yincang}">
          <div class="gwcheader">
              购物车
              <img src="/img/gouwuche/lingdang.png" alt="" @click="goxiaoxi()">
          </div>
          <!--        地址和配送时间选择栏-->
          <div class="dizhishijian">
              <div @click="dizhi()">
                  <img src="/img/gouwuche/dizhi.png" alt="">
                  <span>
                      选择收货地址
                  </span>
                  <img src="/img/gouwuche/zhuanxiang.png" alt="">
              </div>
              <div @click="shijian()">
                  <img src="/img/gouwuche/shijian.png" alt="">
                  <span>
                      选择配送时间
                  </span>
        <img src="/img/gouwuche/zhuanxiang.png" alt="">
        <span style="font-size: 24px;color: #00b7ee;float: right;top:0px">{{xiaoshi}}</span>

              </div>
          </div>
          <!--        购物车主体-->
          <div class="gwcmain">
              <div style="height: 20px;margin: 0;background: none;"></div>
              <div v-for="(data,a) in allgwc" :key="a" :class="{'bianhui' : yincang}">
                  <img src="/img/gouwuche/quanxuan.png" alt="" v-show="xuanze[a]" @click="jianqu(data,a)">
                  <div class="xuanze" v-show="!xuanze[a]" @click="add(data,a)"></div>
        <!-- 长按图片删除商品 -->
                  <div class="shangpin"  @touchstart.prevent="touchin(a)" @touchend.prevent="cleartime()">
                      <img :src="data.img" alt="" style="margin: 0;" >
                  </div>
                  <div class="spjieshao">
                      <p>{{data.name}}</p>
                      <div class="jiage">
                          <div>
                              ￥{{data.jiage}}
                          </div>
                          <div>
                              <div @click="numdown(data,a)">
                                  -
                              </div>
                              <div>
                                  {{data.number}}
                              </div>
                              <div @click="numup(data,a)">
                                  +
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>

          <!--        底部结算-->
          <div class="gwcbottom">
              <div class="bottom-main">
                  <div>
                      <div>
                          <img src="/img/gouwuche/quanxuan.png" alt="" v-show="!quanxuan" @click="qingchu()">
                          <div v-show="quanxuan" @click="thisqx()"></div>
                      </div>
                      <div>
                          全选
                      </div>
                  </div>
                  <div>
                      <div class="tongji">
                          <p>合计<span>(不含运费):</span><span>￥{{alljiage}}</span></p>
                          <p>已优惠:￥0.00</p>
                      </div>
                      <div class="jiesuan" @click="gojiesuan">
                          去结算（{{allnumber}}）
                      </div>
                  </div>
              </div>
          </div>
      </div>
<!--        地址-->
      <div class="dizhi" v-show="yincang">
          <div class="dizhimain" v-show="dizhiyincang">
              <div class="dizhimain-top">
                  <div>
                      我的收货地址
                  </div>
<!--                    跳转到后期添加地址-->
                  <div>
                      <div>
                          <img src="/img/gouwuche/tianjia.png" alt="">
                      </div>
                      <div>
                          新增收货地址
                      </div>
          <div style="margin-left: 30px; color: #00A0E9;" @click="quxiaodizhi()">
            取消
          </div>
                  </div>
              </div>
              <div class="dizhimain-main" v-for="(data,a) in yiyoudizhi" :key="a" @click="godizhi(data)">
                  {{data.dizhi}}
              </div>
          </div>
          <div v-show="timeyincang" class="timemain">
      <div class="riqi">
        <div v-for="(day,a) in day5" :key='a' @click="bg = a" :class="{'bianbai': a===bg}">
          {{day}}
        </div>
      </div>
      <div class="xiaoshi">
        <div v-for="(h,a) in hour" :key="a" @click="timexuanze(h,a)">
          {{h}}
        </div>
      </div>
          </div>
      </div>
    <div :class="{'zhezhao' : yincang}" v-show="yincang"></div>
  </div>

</template>

<script>
export default {
  name: "gouwuche",
  data(){
    return{
      quanxuan: true,
      allgwc:[],
      //将每个商品的总价存在数组中
      xuanze:[],
      alljiage:0,
      allnumber:0,
      //控制隐藏
      yincang: false,
      dizhiyincang: false,
      timeyincang:false,

      //  选中的商品列表
        splist:[],

      yiyoudizhi:[],
      goxinxi:{"dizhi":"","shijian":""},
      xiaoshi:"",
    // 日期界面
    riqi5:[],//年月日
    day5:[],//日
    bg: 0,
    hour:["10:00-11:00","11:00-12:00","12:00-13:00","13:00-14:00","14:00-15:00","15:00-16:00","16:00-17:00","17:00-18:00","18:00-19:00","19:00-20:00","20:00-21:00","21:00-22:00"]
        }
    },
    created(){
        this.$axios.get("/json/gouwuche.json").then(res=>{
            this.allgwc = res.data
            for(var i=0;i<res.data.length;i++){
                this.xuanze.push(false)
            }
        })
  // console.log(this.$store.state.dingdan)
    },
    //离开时解锁页面锁定
    beforeRouteLeave( to,from,next){

        if(from.name == "gouwuche"){
            var mo=function(e){e.preventDefault()}
            document.body.style.overflow=''
            document.addEventListener("touchmove",mo,false)
        }
        next()
    },
    methods:{
      goxiaoxi(){
        this.$router.push("/xiaoxi")
      },
        //全选全不选
        thisqx(){
            this.quanxuan = false
            //先=0 在加上每个商品的总价
            this.alljiage = 0
            this.allnumber = 0
            for(var i=0;i<this.xuanze.length;i++){
                this.xuanze[i] = true
                this.alljiage += this.allgwc[i].jiage*this.allgwc[i].number
                this.allnumber += this.allgwc[i].number
            }
        },
        qingchu(){
            //每个选择改变  总价归0
            for(var i=0;i<this.xuanze.length;i++){
                this.xuanze[i] = false
            }
            this.quanxuan = true
            this.alljiage = 0
            this.allnumber = 0
        },
        //单行选择 此商品等数量城价格 并添加到总价显示
        add(data,a){
            this.xuanze[a] = true
            this.alljiage += data.number*data.jiage
            this.allnumber += data.number
        },
        jianqu(data,a){
            this.xuanze[a] = false
            this.alljiage -= data.number*data.jiage
            this.allnumber -= data.number
        },
        //数量加减
        numup(data,a){
            data.number+=1
            //最少不能低于1个
            if(data.number>0){
                //判断是否已选择此商品  若是已选择择直接改变总价格
                if(this.xuanze[a] == true){
                    this.alljiage += data.jiage
                    this.allnumber += 1
                }
            }else {
                data.number = 1
            }

        },
        numdown(data,a){
            data.number-=1
            if(data.number>0){
                if(this.xuanze[a] == true){
                    this.alljiage -= data.jiage
                    this.allnumber -= 1
                }
            }else {
                data.number = 1
            }
        },


    //    显示地址
        dizhi(){
    this.yincang = true
            this.dizhiyincang = true
            //禁止页面滚动
            var mo=function(e){e.preventDefault()}
            document.body.style.overflow='hidden'
            document.addEventListener("touchmove",mo,false)

            this.$axios.get("/json/dizhi.json").then(res=>{
                // console.log(res)
                this.yiyoudizhi = res.data
            })
        },
        //选择地址时返回原界面
        godizhi(data){
            this.yincang = false
            this.dizhiyincang = false
            //解除页面滚动锁定
            var mo=function(e){e.preventDefault()}
            document.body.style.overflow=''
            document.addEventListener("touchmove",mo,false)

            this.goxinxi.dizhi = data
            // console.log(this.goxinxi)
        },
  // 不选择地址  取消
  quxiaodizhi(){
    this.yincang = false
    this.dizhiyincang = false
    var mo=function(e){e.preventDefault()}
    document.body.style.overflow=''
    document.addEventListener("touchmove",mo,false)
  },


    //    显示时间
  shijian(){
    this.yincang = true
    this.timeyincang = true
    //禁止页面滚动
    var mo=function(e){e.preventDefault()}
    document.body.style.overflow='hidden'
    document.addEventListener("touchmove",mo,false)

    var date = new Date();
    var hours = date.getHours();
    var xingqi= date.getDay();
    var xingqi2 = (xingqi+3)%7
    var xingqi3 = (xingqi+4)%7
    console.log("时："+ hours+"星期："+xingqi)


    var ss = 24 * 60 * 60 * 1000;
    var timestamp = new Date().getTime(); //获取当前时间戳
    this.riqi5 = []
    this.day5 = []
    for(var i=0;i<5;i++){
      var date1 = new Date(ss * i + timestamp) //加上n天的国际标准日期
      var newTime = date1.toLocaleString(); //把日期转换成2018/6/4 下午10:45:19 格式
      var arr = newTime.split(" "); //把2018/6/4提取出来
      var arr2 = arr[0].split('/'); //把年月日数字单独提出来
      var newdate = arr2[0]+"-"+arr2[1]+"-"+arr2[2]
      this.riqi5.push(newdate)
      this.day5.push(arr2[2])
    }
      this.day5[0] = this.day5[0]+"日（今天）"
      this.day5[1] = this.day5[1]+"日（明天）"
      this.day5[2] = this.day5[2]+"日（后天）"
      this.day5[3] = this.day5[3]+"日（星期"+xingqi2+"）"
      this.day5[4] = this.day5[4]+"日（星期"+xingqi3+"）"
      // console.log(this.riqi5)
      // console.log(this.day5)
},
  // 送达时间选择
  timexuanze(h,a){
    var go = false
    if(this.bg == 0){
      var date = new Date();
      var hours = date.getHours();
      var b = hours+5
      if(a+11 <= b){
        alert("配送至少需要5个小时，请选择正确时间！")
      }else{
        go = true
      }
    }else{
      go = true
    }
    if(go){
      this.xiaoshi = h
      this.goxinxi.shijian = this.riqi5[this.bg]+"日"+h
      console.log(this.goxinxi)
      this.yincang = false
      this.timeyincang = false
      var mo=function(e){e.preventDefault()}
      document.body.style.overflow=''
      document.addEventListener("touchmove",mo,false)
    }
  },

  // 结算
  gojiesuan(){
    if(this.allnumber == 0){
      alert("您还未选择商品！")
    }else{
      if(this.goxinxi.dizhi == ""){
        alert("请输入地址!")
      }else{
        if(this.goxinxi.shijian == ""){
          alert("请输入时间！")
        }else{
          this.$router.push("/dingdanxq")
          // 生成订单  全局
          for(var i=0;i<this.xuanze.length;i++) {
            if(this.xuanze[i] == true){
              this.splist.push({name:this.allgwc[i].name,img:this.allgwc[i].img,jiage:this.allgwc[i].jiage,shuliang:this.allgwc[i].number})
            }
          }
            //直接使用store会出现push不能使用
            this.$store.state.shangpinlist = this.splist
          this.$store.state.heji = this.alljiage
          this.$store.state.dingdan = this.goxinxi
          console.log(this.$store.state.dingdan)
        }
      }
    }
  },

  // 长按事件
  touchin(a){
    // console.log(a)
      clearInterval(this.Loop); //再次清空定时器，防止重复注册定时器
      this.Loop = setTimeout(function() {
  if(confirm("从购物车中删除该件商品？")){
    this.allgwc.splice(a,1)
    alert("删除商品成功！")
  }else{
  console.log("取消！")
  }
      }.bind(this), 1000);
      },
      cleartime() {
        // 这个方法主要是用来将每次手指移出之后将计时器清零
        clearInterval(this.Loop);
      }
    }
}
</script>

